@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');
@import url('https://fonts.cdnfonts.com/css/sf-pro-display');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Inter', sans-serif;
}

body.dark{
    background-image: linear-gradient(182deg, rgb(19, 66, 154), rgb(1, 41, 108));
    color: #f6f6f6;
}

.bg-card{
    background-color: rgba(51, 98, 171, 0.24);
}

/* Component Main */
.currentDay {
    width: calc(100% - 0.5px);
}




@media screen and (min-width: 460px) {
    .currentDay {
        width: calc(33% - 3.16667px);
    }
}

@media screen and (min-width: 768px) {
    .currentDay {
        width: calc(25% - 24.5px);
    }
}
/* Component Hourly */
.hourly {
    width: calc(25% - 5px);
}

@media screen and (min-width: 460px) {
    .hourly {
        width: calc(12.5% - 5.75px);
    }
}

@media screen and (min-width: 768px) {
    .hourly {
        width: calc(12.5% - 11px);
    }
}

/* Component This Week */

.card-tw {
    width: calc(50% - 4.5px);
}

@media screen and (min-width: 768px) {
    .card-tw {
        width: calc(33.3333% - 8.5px);
    }
}

